*{
    margin: auto;
     padding: auto;
    background:linear-gradient(rgb(224, 237, 249));
}
.container{
    display:  flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border: 10px groove black;
    width: 38vw;
    margin-top: 30vh;
    
}
textarea{
    margin: 5vh;
    width: 20vw;
    
    height: 18vh;
    background:linear-gradient(white,rgb(237, 229, 215));
    font-size: 1.4em;
    font-family:'Times New Roman', Times, serif;
}
textarea:hover{
    cursor: text;
    color: rgb(8, 49, 83);
    font-weight: 700;
}

.text{
    font-size: 2rem;
    color:rgb(3, 39, 70);
}